import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"


interface State {
  inputValue: string
}

/**
 * This is a React-based component template. The `render()` function is called
 * automatically when your component should be re-rendered.
 */
class RobinChatInput extends StreamlitComponentBase<State> {
  public state = { inputValue: "" }

  public render = (): ReactNode => {
    return (
      <div style={{padding: 20}}>
        <div
          style={{
            border: "solid",
            borderBlockColor: "#2A344F",
            padding: 15,
            borderRadius: 5,
            display: "flex",
            alignItems: "center",
          }}
        >
          <input
            type="text"
            value={this.state.inputValue}
            onChange={this.handleInputChange}
            placeholder="Enter your question here"
            style={{ border: "none", outline: "none", flex: 1 }}
          />
          <div style={{ position: "relative" }}>
            <img
              src="https://i.ibb.co/6X1cVqb/button-upscaled.png"
              style={{ height: 40, marginLeft: 10, transition: "transform 0.5s" }}
              onMouseOver={(e) =>
                ((e.target as HTMLImageElement).style.transform = "rotate(180deg)")
              }
              onMouseOut={(e) =>
                ((e.target as HTMLImageElement).style.transform = "rotate(0deg)")
              }
              onClick={this.handleInputSubmit}
            />
          </div>
        </div>
      </div>
    )
  }

  private handleInputSubmit = (): void => {
    Streamlit.setComponentValue(this.state.inputValue)
    this.setState({ inputValue: "" })
  }

  private handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const userInput = event.target.value
    this.setState({ inputValue: userInput })
  }
}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(RobinChatInput)
